.menu-logo {
    width: 150px;
    height: 150px;
    margin: 30px;
}

.menu-name {
    font-family: UbuntuBold, sans-serif;
    font-size: 29pt;
}

.menu-entry {
    font-family: UbuntuRegular, sans-serif;
    font-size: 20pt;
    margin: 25px auto;
    padding: 20px;
    background-color: #BF1A2F;
    max-width: 400px;
    border-radius: 0.7rem;
    color: white;
}

.menu-head {
    font-family: UbuntuRegular, sans-serif;
    font-size: 14pt;
    color: white;
    margin-left: 0;
}

hr {
    border-width: 3px;
    border-color: white;
}

.menu-head::before {
    content: "<";
    margin-right: 5px;
}

.menu-entry > p {
    font-size: 12pt;
}

a {
    text-decoration: none;
    color: white;
}

a:hover {
    color: snow;
}

.title {
    font-family: Ubuntu, sans-serif;
    font-size: 40pt;
    margin: 20px;
    color: #BF1A2F;
    font-weight: bold;
}

.container {
    max-width: 768px;
}

label {
    font-family: Ubuntu, sans-serif;
    font-size: 150%;
    display: inline;
}




div.btn-container{
    display: table-cell;
    vertical-align: middle;
    text-align: center;
}

label {
    font-size: 25px;
    color: #424242;
    font-weight: 500;
}

.btn-color-mode-switch{
    display: inline-block;
    margin: 0;
    position: relative;
}

.btn-color-mode-switch > label.btn-color-mode-switch-inner{
    margin: 0;
    width: 190px;
    height: 50px;
    background: rgb(0, 175, 11);
    border-radius: 0.7rem;
    overflow: hidden;
    position: relative;
    transition: all 0.3s ease;
    /*box-shadow: 0px 0px 8px 0px rgba(17, 17, 17, 0.34) inset;*/
    display: block;
}

.btn-color-mode-switch > label.btn-color-mode-switch-inner:before{
    content: attr(data-on);
    position: absolute;
    font-size: 25px;
    font-weight: 500;
    top: 7px;
    right: 25px;

}

.btn-color-mode-switch > label.btn-color-mode-switch-inner:after{
    content: attr(data-off);
    width: 85px;
    height: 35px;
    background: #2cc71e;
    border-radius: 0.7rem;
    position: absolute;
    left: 7px;
    top: 8px;
    text-align: center;
    transition: all 0.3s ease;
    box-shadow: 0 0 6px -2px #111;
}

.btn-color-mode-switch > .alert{
    display: none;
    background: #FF9800;
    border: none;
    color: #fff;
}

.btn-color-mode-switch input[type="checkbox"]{
    cursor: pointer;
    width: 50px;
    height: 25px;
    opacity: 0;
    position: absolute;
    top: 0;
    z-index: 1;
    margin: 0;
}

.btn-color-mode-switch input[type="checkbox"]:checked + label.btn-color-mode-switch-inner{
    background: #710303;
    color: #fff;
}

.btn-color-mode-switch input[type="checkbox"]:checked + label.btn-color-mode-switch-inner:after{
    content: attr(data-on);
    left: 100px;
    background: #8c1414;
}

.btn-color-mode-switch input[type="checkbox"]:checked + label.btn-color-mode-switch-inner:before{
    content: attr(data-off);
    right: auto;
    left: 20px;
}

.btn-color-mode-switch input[type="checkbox"]:checked ~ .alert{
    display: block;
}