.rulesText{
    font-size: 1rem;
    text-align: justify;
}

ul{
    margin-top: 0;
}
p{
    margin-bottom: 5px;
}

@media print {
    .main-menu {
        display: none;
    }
    body {
        background-color: white;
    }
    .rules::before {
        content: "Rules of the game GeoQuizzr";
        display: block;
        background-color: white;
        color: black;
        max-width: 100%;
    }
    .menu-entry-print {
        background-color: white;
        color: black;
        max-width: 100%;
    }
}