#game-panel {
    display: none;
}

.answer {
    display: inline-block;
    margin: 10px;
    padding: 15px;
    border: #282c34 solid 2px;
    border-radius: 0.7rem;
    cursor: grab;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
    font-size: 17pt;
}

.image {
    max-width: 80%;
    height: auto;
    display: block;
    margin: auto;
    padding: 30px;
}

.modal.fade.show {
    backdrop-filter: blur(5px);
}

.drag-sort-enable {
    max-width: 350px;
    margin: auto;
}

@media screen and (max-width: 968px)  {
    .answer {
        margin: 5px;
        padding: 10px;
        font-size: 13pt;
    }

}

@media screen and (max-width: 592px)  {
    .answer {
        margin: 3px;
        padding: 7px;
        font-size: 12px;
    }

}

#imgLevel2{
    padding-top: 0;
}
.countryName{
    font-size: 2rem;
    margin-top: 1rem;
    margin-bottom: 1rem;
}

#backButton{
    display: none;
}
#hintButton{
    display: none;
}
.modalButtons{
    display: flex;
    flex-direction: row;
    gap: 8px;
}
.modal-footer{
    justify-content: space-between;
}
#hintClose{
    display: none;
}


.list {
    counter-reset: li;
    list-style: none;
    *list-style: decimal;
    padding: 0;
    margin-bottom: 1rem;
}

.listItem{
    position: relative;
    display: block;
    padding: .4em .4em .4em .4em;
    margin: .5em 0;
    line-height: 2rem;
    background: #ddd;
    color: #444;
    text-decoration: none;
    border-radius: .3em;
    transition: all .3s ease-out;
}

.listItem:before{
    content: counter(li);
    counter-increment: li;
    position: absolute;
    left: -2em;
    top: 50%;
    margin-top: -1.5em;
    background: #018E42;
    color: white;
    height: 3em;
    width: 3em;
    line-height: 2.3em;
    border: .3em solid #fff;
    text-align: center;
    font-weight: bold;
    border-radius: 50%;
    transition: all .3s ease-out;
}

